import {dataConst} from './defaultData';

export default (editor, opts = {}) => {
    const { block, label, id } = opts;

    editor.BlockManager.add(id, {
        label: label,
        content: { type: id },
        media: dataConst.iconNavbar,
        category: 'Navigations',
        select: true,
        ...block,
    });

    editor.BlockManager.add(`${id}-navbar-item`, {
        label: `Navbar Item`,
        content: { type: `${id}-navbar-item` },
        media: dataConst.iconNavbarItem,
        category: 'Navigations',
        select: true,
    });

    // editor.BlockManager.add(`${id}-navbar-item-link`, {
    //     label: editor.I18n.t("grapejs-plugin-bootstrap-navbar.linkMenu"),
    //     content: { type: `${id}-navbar-item-link` },
    //     media: dataConst.iconLinkMenu,
    //     category: 'Navigations',
    //     select: true,
    // })

    editor.BlockManager.add(`link-advance`, {
        label: editor.I18n.t("grapejs-plugin-bootstrap-navbar.linkAdvance"),
        content: { type: `link-advance` },
        media: dataConst.iconLinkAdvance,
        category: 'Navigations',
        select: true,
    })
}
