import { dataConst } from './defaultData';

function makeid(length) {
    let result = '';
    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    const charactersLength = characters.length;
    let counter = 0;
    while (counter < length) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
        counter += 1;
    }
    return result;
}

export default (editor, opts = {}) => {
    const { id, label, classPrefix, menus, prefixPath } = opts;
    const idAttrNavbarCollapse = `${id}-${makeid(10)}`;

    // logo
    const idLogo = `${id}-logo`;

    editor.DomComponents.addType(idLogo, {
        model: {
            defaults: {
                tagName: 'a',
                attributes: {
                    class: `${classPrefix}-brand d-flex align-items-center`,
                    "data-x-cls": "navbar-logo",
                    href: '#',
                },
                components: [
                    {
                        type: 'image',
                        tagName: 'img',
                        attributes: {
                            width: 60,
                            height: 60,
                            src: dataConst.srcLogo,
                            class: 'd-inline-block align-top',
                            alt: 'Logo',
                        },
                        traits: [
                            'alt', 'width', 'height',
                        ]
                    },
                    {
                        type: 'text',
                        tagName: 'span',
                        attributes: {
                            class: "ml-1",
                        },
                        components: `Brand`,
                    },
                ],
                traits: [
                    'href', 'alt',
                ]
            }
        },
        isComponent: function(el){
            return $(el).attr('data-x-cls') === 'navbar-logo';
        },
    })
    // -- logo

    // button
    const idButtonExpand = `${id}-button-expand`;
    editor.DomComponents.addType(idButtonExpand, {
        model: {
            defaults: {
                tagName: 'button',
                editable: false,
                attributes: {
                    class: `${classPrefix}-toggler`,
                    'type': "button",
                    'data-toggle': 'collapse',
                    'data-target': `#${idAttrNavbarCollapse}`,
                    'aria-controls': `${idAttrNavbarCollapse}`,
                    'aria-expanded': "false",
                    'aria-label': "Toggle navigation",
                },
                components: {
                    tagName: 'span',
                    attributes: {
                        class: 'navbar-toggler-icon',
                    }
                },
            },
        }
    })
    // -- button

    // Navbar collapse
    const idNavbarCollapse = `${id}-navbar-collapse`;
    const idNavbarNav = `${id}-navbar-nav`;

    editor.DomComponents.addType(idNavbarCollapse, {
        model: {
            defaults: {
                name: `${id}-navbar-collapse`,
                tagName: 'div',
                attributes: {
                    id: idAttrNavbarCollapse,
                    class: `collapse ${classPrefix}-collapse`
                },
                components: [
                    { type: idNavbarNav, }
                ]
            }
        }
    });

    const idNavbarNavItem = `${id}-navbar-item`;

    editor.DomComponents.addType(idNavbarNav, {
        model: {
            defaults: {
                name: `${id}-navbar-nav`,
                tagName: 'ul',
                attributes: {
                    class: `${classPrefix}-nav mr-auto`
                },
                components: [
                    { type: idNavbarNavItem },
                ],
                droppable: (target, destination) => {
                    return target.get('type') === idNavbarNavItem;
                },
            },
        }
    })

    const idLinkMenus = `${id}-navbar-item-link`;

    editor.DomComponents.addType(idNavbarNavItem, {
        model: {
            defaults: {
                name: `${id}-navbar-nav-item`,
                tagName: 'li',
                attributes: {
                    class: `nav-item`,
                },
                components: [
                    { type: idLinkMenus },
                ],
                draggable: (target, destination) => {
                    return destination.get('type') === idNavbarNav;
                },
                droppable: (target, destination) => {
                    return target.get('type') === idLinkMenus;
                },
            }
        }
    })

    const idLinkAdvance = `link-advance`;
    const traitOnPageIdx = 'on-page-href';
    const traitOnPage = {
        name: traitOnPageIdx,
        label: editor.I18n.t('grapejs-plugin-bootstrap-navbar.onPageMenu'),
        type: 'select',
        options: [
            {id: '#', name: '-'},
            ...menus.map(
            (item) => {
                if (prefixPath && !item.id.startsWith(prefixPath)) item.id = prefixPath + item.id;
                return item;
            }
        )],
        changeProp: true,
    }
    const traitFragmentIdentifyIdx = 'fragment-identifier';
    const traitFragmentIdentify = {
        name: traitFragmentIdentifyIdx,
        label: editor.I18n.t('grapejs-plugin-bootstrap-navbar.fragId'),
        type: 'text',
        placeholder: 'idx-block-about-us',
        changeProp: true,
    }
    const traitOffSiteLinkIdx = 'off-site-link';
    const traitOffSiteLink = {
        name: traitOffSiteLinkIdx,
        label: editor.I18n.t('grapejs-plugin-bootstrap-navbar.offSite'),
        type: 'text',
        placeholder: 'https://bflow.vn',
        changeProp: true,
    }
    editor.DomComponents.addType(idLinkAdvance, {
        extend: 'link',
        model: {
            defaults: {
                name: 'Link Advance',
                tagName: 'a',
                draggable: true,
                droppable: false,

                href: prefixPath + '/',
                "type-link": "on-page-href",
                "on-page-href": prefixPath + '/',
                "fragment-identifier": "",
                "off-site-link": "",
                traits: [
                    'target',
                    {
                        name: 'type-link',
                        label: editor.I18n.t('grapejs-plugin-bootstrap-navbar.typeOfLink'),
                        type: 'select',
                        options: [
                            {
                                id: 'on-page-href',
                                name: editor.I18n.t('grapejs-plugin-bootstrap-navbar.onPageMenu'),
                            },
                            {
                                id: 'fragment-identifier',
                                name: editor.I18n.t('grapejs-plugin-bootstrap-navbar.fragId'),
                            }, {
                                id: 'off-site-link',
                                name: editor.I18n.t('grapejs-plugin-bootstrap-navbar.offSite'),
                            },
                        ],
                        changeProp: true,
                    },
                    traitOnPage,
                ],
                'script-props': ['href', 'type-link', 'fragment-identifier', 'off-site-link'],
                content: editor.I18n.t("grapejs-plugin-bootstrap-navbar.linkTxt"),
            },
            init() {
                this.on('change:type-link', this.handleChangeTypeLink);
                this.on('change:on-page-href', this.handleChangeOnPageLink);
                this.on('change:fragment-identifier', this.handleChangeFragmentLink);
                this.on('change:off-site-link', this.handleChangeOffSiteLink);
            },
            handleNavLinkHrefTypeChange() {
                this.setAttributes({ 'href': this.get('href') });
            },
            resetTraintAdvance() {
                this.removeTrait(traitOnPageIdx);
                this.removeTrait(traitFragmentIdentifyIdx);
                this.removeTrait(traitOffSiteLinkIdx);
            },
            handleChangeTypeLink() {
                this.resetTraintAdvance();

                let typeLink = this.get('type-link');
                if (typeLink === 'on-page-href') this.addTrait(traitOnPage);
                if (typeLink === "fragment-identifier") this.addTrait(traitFragmentIdentify);
                if (typeLink === 'off-site-link') this.addTrait(traitOffSiteLink);
            },
            handleChangeOnPageLink() {
                this.setAttributes({ 'href': this.attributes?.["on-page-href"] });
            },
            handleChangeFragmentLink(){
                this.setAttributes({'href': '#' + this.attributes?.['fragment-identifier']});
            },
            handleChangeOffSiteLink(){
                let changedData = this.attributes?.['off-site-link'] || null;
                if (changedData && (changedData.startsWith('http://') || changedData.startsWith('https://'))){
                    this.setAttributes({'href': changedData});
                }
            },
        }
    })

    editor.DomComponents.addType(idLinkMenus, {
        extend: idLinkAdvance,
        model: {
            defaults: {
                name: `${id}-navbar-item-link`,
                attributes: {
                    class: 'nav-link',
                    href: prefixPath + '/',
                },
                // allow drag to any type.
                draggable: (target, destination) => {
                    return destination.get('type') === idNavbarNavItem;
                },
                components: [
                    {
                        type: 'text',
                        content: `${editor.I18n.t("grapejs-plugin-bootstrap-navbar.homeTxt")} <span class="sr-only">(current)</span>`,
                    },
                ],
            },
        }
    })

    // -- Navbar collapse

    // INIT
    editor.DomComponents.addType(id, {
        model: {
            defaults: {
                name: label,
                tagName: 'nav',
                droppable: false,
                attributes: {
                    class: `${classPrefix} ${classPrefix}-expand-lg ${classPrefix}-light bg-light`,
                },
                components: [
                    { type: idLogo },
                    {
                        tagName: 'div',
                        components: { type: idButtonExpand },
                    },
                    { type: idNavbarCollapse },
                ],
                navColor: 'navbar-light',
                bgColor: 'bg-light',
                traits: [
                    {
                        name: 'navColor',
                        label: 'Navbar Color',
                        type: 'select',
                        options: [
                            { id: 'navbar-light', name: 'Light', selected: true },
                            { id: 'navbar-dark', name: 'Dark' },
                        ],
                        changeProp: true,
                    },
                    {
                        name: 'bgColor',
                        label: 'Background Color',
                        type: 'select',
                        options: [
                            { id: 'bg-light', name: 'Light', selected: true },
                            { id: 'bg-dark', name: 'Dark' },
                            { id: 'bg-primary', name: 'Primary' },
                            { id: 'bg-secondary', name: 'Secondary' },
                            { id: 'bg-success', name: 'Success' },
                            { id: 'bg-danger', name: 'Danger' },
                            { id: 'bg-warning', name: 'Warning' },
                            { id: 'bg-info', name: 'Info' },
                        ],
                        changeProp: true,
                    },
                ],
                'script-props': ['navColor', 'bgColor'],
                script: function () {
                    $(document).ready(function () {
                        let currentPath = window.location.pathname;
                        $(this).find('.nav-link').each(function () {
                            let navItem = $(this).closest('.nav-item');
                            navItem.removeClass('active');

                            let link = $(this).attr('href');
                            if (link === currentPath) {
                                $(navItem).addClass('active')
                            }
                        });
                    })
                },
            },
            init() {
                this.on('change:navColor', this.handleNavColorTypeChange);
                this.on('change:bgColor', this.handleBackgroundColorTypeChange);

                // push id to data-target & aria-controls
                let navbarCollapseIdx = this.components().models[2].getAttributes().id;
                this.components().models[1].setAttributes({
                    "data-target": `#${navbarCollapseIdx}`,
                    "aria-controls": navbarCollapseIdx,
                });
            },
            handleNavColorTypeChange() {
                let bgToColor = this.get('navColor');
                let allKey = this.traits.get('navColor').attributes.options.map((item) => item.id);
                this.setAttributes({ class: this.resetClass(this.getAttributes().class, allKey, bgToColor) });
            },
            handleBackgroundColorTypeChange() {
                let bgToColor = this.get('bgColor');
                let allKey = this.traits.get('bgColor').attributes.options.map((item) => item.id);
                this.setAttributes({ class: this.resetClass(this.getAttributes().class, allKey, bgToColor) });
            },

            resetClass(classCurrent, classListAlter, appedData = '') {
                classListAlter.map(
                    (key) => {
                        classCurrent = classCurrent.replaceAll(key, '');
                    }
                )
                classCurrent += ` ${appedData}`;
                return classCurrent;
            }
        },
    });
    // -- INIT
};
